body, html {
  /*take up all available height*/
  height: 100%;
    background: white;

}








.flex-container {
   display: -ms-flexbox;
   display: flex;
   -ms-flex-direction: row;
       flex-direction: row;

}






.flex-col {
  width:33%;
  border:2px solid white;
  border-radius: 5px;
  margin:10px;
  display: inline-block;
  position: relative;
  padding: .5em;
  background: #fff;
  -webkit-box-shadow: 0 2px 3px rgba(0,0,0,0.5);
          box-shadow: 0 2px 3px rgba(0,0,0,0.5);

}

.projTitle {
  font-size: 10px;
}


.text {
  background: rgba(0,0,0,0.8);
  color: #fff;
  -webkit-transition: opacity 0.6s;
  -o-transition: opacity 0.6s;
  transition: opacity 0.6s;
  opacity: 0;
  position: absolute;
  top: .5em; bottom: .5em; left: .5em; right: .5em;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  font-size:8px;
  padding:10px;

}
.flex-col:hover .text {
  opacity: 1;

}



img {
  max-width: 100%;
  display: block;
}



